<template>
  <component
    v-if="currentItinerary && setting"
    :is="template"
    v-bind="currentItinerary"
    :price_per_person="setting.price_per_person"
    :if_show_logo="setting.if_show_logo"
    :if_show_signup="setting.if_show_signup"
    :if_show_locations="setting.if_show_locations"
    :locations="setting.locations"
    :imgs="setting.imgs"
  ></component>
</template>

<script>
import { defineAsyncComponent, shallowRef } from 'vue'

export default {
  data () {
    return {
      setting: null,
      currentItinerary: null,
      template: shallowRef(defineAsyncComponent(() => { return import('@/views/LayoutOne.vue') }))
    }
  },
  methods: {
    get_setting () {
      let settingUrl = 'https://blooming-savannah-00156.herokuapp.com/api' + '/edm/setting/' + '62ba834ec814e33c181a61a6'
      settingUrl = 'https://lara-unitrip-dev-by4xskwu4q-de.a.run.app/api' + '/dm/setting/' + '62ba834ec814e33c181a61a6'
      this.$http.get(settingUrl).then(response => {
        this.setting = response.data
        console.log(this.setting, 'setting')
        if (!response.data.is_display) {
          this.$router.push({ name: '404' })
        }
      }).catch(error => {
        console.dir(error)
        this.$router.push({ name: '404' })
      })
    },
    get_itinerary () {
      let itineraryUrl = 'https://blooming-savannah-00156.herokuapp.com/api' + '/edm/group-itinerary/' + '62566ad0cf4098f58379c814'
      itineraryUrl = 'https://lara-unitrip-dev-by4xskwu4q-de.a.run.app/api' + '/dm/group-itinerary/' + '62ba834ec814e33c181a61a6'
      this.$http.get(itineraryUrl).then(response => {
        this.currentItinerary = response.data
        console.log(this.currentItinerary, 'travelData')
      }).catch(error => {
        console.log(error)
      })
    }
  },
  created () {
    this.get_setting()
    this.get_itinerary()
  }
}
</script>
